/* eslint-disable no-console */
import core from '@actions/core';
import github, { context } from '@actions/github';
import { Context } from '@actions/github/lib/context.js';
import { OctokitResponse } from '@octokit/types';
import bolt from '@slack/bolt';

type ActionConfig = {
  app_name: string,
  build_number: string,
  build_type: string,
  build_version: string,
  github_token: string,
  notes: string,
  include_secrets: string,
  slackbot_channel: string,
  slackbot_secret: string,
  slackbot_token: string,
  status: string,
  title: string,
  ts: string,
  thread_ts: string
}

const inputs: Array<keyof ActionConfig> = [
  'app_name',
  'build_number',
  'build_type',
  'build_version',
  'github_token',
  'notes',
  'include_secrets',
  'slackbot_channel',
  'slackbot_secret',
  'slackbot_token',
  'status',
  'title',
  'ts',
]

const getActionConfig = (): ActionConfig => {
  let config: ActionConfig = JSON.parse(core.getInput('config')) 

  return inputs.reduce((c, input) => {
    const value = core.getInput(input)

    c[input] = value || c[input] || ''

    return c
  }, config)
}

const {
  app_name: appName,
  build_number: number,
  build_type: type,
  build_version: version,
  github_token: githubToken,
  notes,
  include_secrets: includeSecrets,
  slackbot_channel: slackbotChannel,
  slackbot_secret: slackbotSecret,
  slackbot_token: slackbotToken,
  status,
  title,
  ts,
} = getActionConfig()

core.debug(JSON.stringify(getActionConfig(), null, 2))

const { App } = bolt

const app = new App({
  signingSecret: slackbotSecret,
  token: slackbotToken,
})

const messageConfig: MessageConfig = {
  channel: slackbotChannel,
  ts,
}

const octokit = github.getOctokit(githubToken)

const updateSlackChannel = async () => {
  try {
    const commit = await getCommit()
    const { data: actor } = await getActor()
    const message = NewBuildMessage({
      actor,
      appName,
      commit,
      context: github.context,
      messageConfig,
      notes,
      number,
      status,
      title,
      type,
      version,
    })
    const method = messageConfig.ts ? 'update' : 'postMessage'
    const response = await app.client.chat[method](message)
    const { ts } = response
    const secrets = /false/.test(includeSecrets) // false || 'false'
      ? {}
      : {
          github_token: githubToken,
          slackbot_channel: slackbotChannel,
          slackbot_secret: slackbotSecret,
          slackbot_token: slackbotToken,
        }

    return core.setOutput(
      'config',
      JSON.stringify({
        app_name: appName,
        build_number: number,
        build_type: type,
        build_version: version,
        include_secrets: includeSecrets,
        notes,
        status,
        title,
        ts,
        ...secrets,
      })
    )
  } catch (e) {
    console.error(e)
  }
}

const getActor = () => octokit.rest.users.getByUsername({ username: context.actor })
const getCommit = () =>
  octokit.rest.git.getCommit({
    ...github.context.repo,
    commit_sha: github.context.sha,
  })



const NewBuildMessage = ({
  actor,
  appName,
  commit,
  context,
  messageConfig,
  notes,
  number,
  status,
  title,
  type,
  version,
}: {
  actor: Actor['data'],
  appName: string,
  commit: Commit,
  context: Context,
  messageConfig: MessageConfig,
  notes: any,
  number: any,
  status: any,
  title: any,
  type: any,
  version: any,
}) => {
  const { data } = commit
  const { message } = data
  const refString = context.ref.replace('refs/heads/', '')
  const fields = [
    type && `*Type:*\n${type}`,
    number && `*Number:*\n${number}`,
    appName && `*App:*\n${appName}`,
    version && `*Version:*\n${version}`,
    `*Triggered by:*\n${actor.name}`,
    `*Ref:*\n<${buildBaseUrl(context)}/tree/${refString}|${refString}>`,
    `*SHA:*\n*<${commit.data.html_url}|${context.sha.slice(-8)}>*`,
    `*Commit*\n${message}`,
    notes && `*Notes*\n${notes}`,
    generateStatusMessage(status),
    `*Follow updates here*\n<${buildBaseUrl(context)}/actions/runs/${
      context.runId
    }|Link to updates>`,
  ]

  return {
    ...messageConfig,
    text: title,
    blocks: [
      {
        type: 'header',
        text: {
          type: 'plain_text',
          text: title,
        },
      },
      {
        type: 'section',
        fields: fields
          .filter(f => f)
          .map(text => ({
            type: 'mrkdwn',
            text,
          })),
        accessory: {
          type: 'image',
          image_url:
            actor?.avatar_url ??
            'https://avatars.slack-edge.com/2022-03-10/3247522189504_4ef1b59856a2817b2fa8_512.png',
          alt_text: 'author image',
        },
      },
    ],
  }
}

const generateStatusMessage = (status = 'working') =>
  ({
    success: ':pico-success: *Build Succeeded*',
    failure: ':pico-fail: *Build failed*',
    working: ':pico-working: *Building*',
    cancelled: ':pico-fail: *Build cancelled*',
  }[status.toLowerCase()])

const buildBaseUrl = ({ serverUrl, repo }: { serverUrl: string; repo: { owner: string; repo: string } }) => `${serverUrl}/${repo.owner}/${repo.repo}`

updateSlackChannel()

type MessageConfig = {
  channel: string; 
  ts: string 
}

type Actor = OctokitResponse<{
  login: string;
  id: number;
  node_id: string;
  avatar_url: string;
  gravatar_id: string | null;
  url: string;
  html_url: string;
  followers_url: string;
  following_url: string;
  gists_url: string;
  starred_url: string;
  subscriptions_url: string;
  organizations_url: string;
  repos_url: string;
  events_url: string;
  received_events_url: string;
  type: string;
  site_admin: boolean;
  name: string | null;
}>

type Commit = OctokitResponse<{
  sha: string;
  node_id: string;
  url: string;
  author: {
    date: string;
    email: string;
    name: string;
  };
  committer: {
    date: string;
    email: string;
    name: string;
  };
  message: string;
  tree: {
    sha: string;
    url: string;
  };
  html_url: string;
}>